
.dashboardSummary {
  margin: 2rem 0;
}

.dashboardSummary > div {
  display: flex;
  justify-content: center;
  background-color: #fff;
}

.dashboardSummary > div > p {
  background-color: rgba(70, 117, 218, 0.932);
  color: #fff;
  width: 100%;
  margin: 0 2rem;
  text-align: center;
  font: 300 1.3rem "Roboto";
  padding: 1.5rem;
}

.dashboardSummaryBox2 > a {
  color: rgb(0, 0, 0);
  font: 300 1.3rem "Roboto";
  text-align: center;
  background-color: rgb(255, 233, 174);
  text-decoration: none;
  padding: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 8vmax;
  width: 8vmax;
  margin: 2rem;
  border-radius: 100%;
}

.dashboardSummaryBox2 > a:first-child {
  background-color: rgb(255, 110, 110);
  color: #fff;
}

.dashboardSummaryBox2 > a:last-child {
  background-color: rgb(51, 51, 51);
  color: #fff;
}



.charts {
  display: grid;
  grid-template-columns: 2fr 1fr;
  place-items: center;
}

.lineChart {
  width: 95%;
}

.doughnutChart {
  width: 90%;
}

@media screen and (max-width: 600px) {
  .dashboard {
    grid-template-columns: 1fr;
  }
  .dashboardContainer {
    margin-left: 0;
    width: 100vw;
  }
  .dashboardSummary > div > p {
    margin: 0;
  }
  .dashboardSummaryBox2 > a {
    margin: 0.5rem;
  }

  .charts {
    display: grid;
    grid-template-columns: 1fr;
    place-items: center;
  }
  .lineChart {
    width: 95%;
    height: 130%;
  }
  .lineChart > * {
    height: 100% !important;
  }
  .doughnutChart {
    width: 80%;
  }
}
